"use client"

import { useState } from "react";

export default function CustomFileUpload() {
  const [fileName, setFileName] = useState("No file chosen");

  const handleFileChange = (event) => {
    if (event.target.files.length > 0) {
      setFileName(event.target.files[0].name);
    } else {
      setFileName("No file chosen");
    }
  };

  return (
    <div className="flex items-center border border-gray-300 py-2 px-3 rounded-md w-fit">
      {/* Hidden File Input */}
      <input
        type="file"
        id="fileInput"
        className="hidden"
        onChange={handleFileChange}
      />

      {/* Custom Choose File Button */}
      <label
        htmlFor="fileInput"
        className="bg-blue-500 text-white py-1 px-4 rounded-md cursor-pointer hover:bg-blue-700"
      >
        Choose File
      </label>

      {/* Display Selected File Name */}
      <span className="ml-3 text-gray-600">{fileName}</span>
    </div>
  );
}
